import React, { useState, useEffect } from 'react'

import { getUsers } from '../../utils/admin'

import styles from './index.module.css'

const AdminAuthenticated = () => {
  const [ users, setUsers ] = useState([])

  useEffect(() => {
    const fetchUsers = async () => {
      const res = await getUsers()

      const cleanedUsers = res.data.data.map(user => (
        {
          id: user.id,
          first_name: user.user.first_name,
          last_name: user.user.last_name,
          nick_name: user.user.nick_name,
          email: user.user.email,
          note: user.note,
          gender: user.user.gender,
          status: user.payment.status,
          payment_id: user.payment.id,
          reference_code: user.payment.reference_code,
          form_of_payment: user.payment.form_of_payment,
          paid_at: user.payment.paid_at
        }
      ))

      setUsers(cleanedUsers)
        console.log(cleanedUsers)
    }

  fetchUsers()

  }, [])

  let options  = {
    keyColumn: 'id',
    data: {
        columns: [
            {
                id: "id",
                label: "id",
                colSize: "10px"
            },
            {
                id: "first_name",
                label: "First Name",
                colSize: "40px"
            },
            {
                id: "last_name",
                label: "Last Name",
                colSize: "40px"
            },
            {
              id: "nick_name",
              label: "Nick Name",
              colSize: "40px"
            },
            {
              id: "email",
              label: "Email",
              colSize: "50px"
            },
            {
              id: "status",
              label: "status",
              colSize: "20px"
            },
            {
              id: "form_of_payment",
              label: "Payment Method",
              colSize: "40px"
            },
            {
              id: "paid_at",
              label: "Paid At",
              colSize: "50px"
            },
            {
              id: "reference_code",
              label: "Ref Code",
              colSize: "25px"
            },
            {
              id: "note",
              label: "Note",
              colSize: "50px"
            },
        ],
        rows: users
    }
  }

  return (
    <div className={styles.adminTableContainer}>
    </div>
  )
}

export default AdminAuthenticated

