import React, { useState } from 'react'

import AdminAuthenticated from '../components/adminAuthenticated'
import AdminNothAuthenicated from '../components/adminNotAuthenticated'

const Admin = () => {

  const [ isAuthenticated, setAuthenticated ] = useState(false)

  const checkPassword = (pw) => pw === process.env.ADMIN_PASSWORD ? setAuthenticated(true) : setAuthenticated(false)

  return (
    <div>
    {
      isAuthenticated ? (
        <AdminAuthenticated/>
      ) : (
        <AdminNothAuthenicated checkPassword={checkPassword}/>
      )
    }
    </div>
  )
}

export default Admin