import axios from 'axios'
import {navigate} from 'gatsby'


export const getUsers = () => (
  axios.get(process.env.SERVER_URL_GET_USERS)
    .then(res => res)
    .catch(err => navigate('/404', err))
)

export const updateUserPayment = ({id, status, form_of_payment, paid_at, note}) => (
  axios.put(process.env.SERVER_URL_UPDATE_USER_PAYMENT,
    {
      id,
      status,
      form_of_payment,
      paid_at,
      note
    })
    .then(res=> console.log('User Updated', res))
    .catch(err => console.log('User not updated', err))
)